@charset "GBK";

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote, 
dl,
dt,
dd,
ul,
ol,
li, 
pre, 
form,
fieldset,
legend,
button,
input,
textarea, 
th,
td, 
img { border:medium none; margin: 0; padding: 0; }

body,
button,
input,
select,
textarea { font:12px/1.5  "SimSun", Tahoma, Arial, sans-serif}
input,
select,
form img,
button { vertical-align:middle; }
h1,
h2,
h3,
h4,
h5,
h6 { font-size: 100%; font-weight: normal;}
em,
i
{ font-style:normal; }


ul,
ol { list-style: none; }

a { text-decoration: none; }
a:hover { text-decoration: underline;}

img { border:none; }

table { border-collapse: collapse; border-spacing: 0; }

.clearfix:after { visibility: hidden; display: block; font-size: 0; content: " "; clear: both; height: 0; }
* html .clearfix { zoom: 1; } 
*:first-child+html .clearfix { zoom: 1; } 
textarea, input{resize:none}
a{outline:none}
.clear { clear:both; font-size:0; line-height:0; height:0; overflow:hidden; }
.coz{ clear:both; overflow:hidden; zoom:1}


.word_break { white-space:normal; word-wrap:break-word; word-break:break-all; }
