body{color: #000;}

body.bg-f8f{background-color: #f8f8f8;}


a{
    color: #000;
    transition: all 0.2s linear;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    &:hover{
        color:#333;
    }
}


input, button, textarea, select {
    border: 0;
    border-radius: 0;
    background: none;
    
